import styled, { css } from 'styled-components'
import theme from '@style/theme'

export const Container = styled.div`
  max-width: 2000px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;

  @media (min-width: ${theme.screen.xs}) {
    max-width: 540px;
  }

  @media (min-width: ${theme.screen.sm}) {
    max-width: 720px;
  }

  @media (min-width: ${theme.screen.md}) {
    max-width: 960px;
  }

  @media (min-width: ${theme.screen.lg}) {
    max-width: 1200px;
  }

  @media (min-width: ${theme.screen.xl}) {
    max-width: 2000px;
  }

  ${props =>
    props.fluid &&
    `
    max-width: 2000px !important;
  `};
`

export const Section = styled.section`
  padding: 90px 0;
  overflow: hidden;
`

export const H1 = styled.p`
  ${theme.fontSize.xxl};
  font-family: ${theme.font.primary};
`

export const H2 = styled.p`
  ${theme.fontSize.xl};
  font-family: ${theme.font.primary};
`

export const H3 = styled.p`
  ${theme.fontSize.lg};
  font-family: ${theme.font.primary};
`

export const Button = styled.a`
  display: inline-block;
  padding: 9px 25px;
  margin: 0 10px 10px 0;
  width: auto;
  background: ${theme.color.grayscale.S900};
  color: ${theme.color.grayscale.S100} !important;
  border: 2px solid ${theme.color.grayscale.S900};
  :hover {
    background: ${theme.color.black.regular} !important;
    color: ${theme.color.grayscale.S100} !important;
    border: 2px solid ${theme.color.black.regular};
  }
  a:hover {
    text-decoration: none !important;
  }
  ${props =>
    props.primary &&
    css`
      background: ${theme.color.grayscale.S1000};
      color: ${theme.color.grayscale.S100} !important;
      border: 2px solid ${theme.color.grayscale.S1000};
      :hover {
        background: ${theme.color.black.regular} !important;
        color: ${theme.color.grayscale.S100} !important;
        border: 2px solid ${theme.color.black.regular};
      }
    `};
`
